<template>
	<v-card class="ma-0 pa-0">
		<v-row no-gutters class="mt-3">
			<span no-gutters class="ml-0"> ${{ order.price }} </span>
			<v-btn small depressed class="ml-12 mr-3 mb-3" color="blue darken-2" @click="clickNew()" dark>
				New
			</v-btn>
			<v-btn small depressed class="mr-3 mb-3" color="primary" @click="clickEdit()"> Edit </v-btn>
			<v-btn small depressed class="mr-3 mb-3" color="red darken-2" @click="clickDelete()" dark>
				Delete
			</v-btn>
		</v-row>

		<v-divider class="mt-1"></v-divider>

		<div class="mt-7 mb-5">
			<v-row>
				<v-col cols="3" class="mt-0 pt-0 mb-0 pb-0">
					<v-text-field v-model="order.symbol" label="symbol"></v-text-field>
				</v-col>
				<v-col cols="3" class="mt-0 pt-0 mb-0 pb-0 ml-0 mr-0">
					<!-- <v-text-field v-model="order.side" label="Side"></v-text-field> -->
					<div>
						<small>side : {{ order.side }}</small>
					</div>
					<v-btn
						depressed
						x-small
						class="rounded-r-0"
						:color="order.side === 'Buy' ? 'green lighten-3' : null"
						@click="order.side = 'Buy'"
						><small>Buy</small></v-btn
					>
					<v-btn
						depressed
						x-small
						class="rounded-l-0"
						:color="order.side === 'Sell' ? 'red lighten-3' : null"
						@click="order.side = 'Sell'"
						><small>Sell</small></v-btn
					>
				</v-col>

				<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
					<v-text-field v-model="order.amt" label="amt"></v-text-field>
				</v-col>

				<v-col cols="3" class="mt-0 pt-0 mb-0 pb-0">
					<div><small>buy percent :</small> {{ order.bpt }}</div>
					<v-btn
						depressed
						x-small
						class="rounded-r-0"
						:color="order.bpt === -0.05 ? 'grey lighten-2' : null"
						@click="order.bpt = -0.05"
						><small>0.05</small></v-btn
					>
					<v-btn
						depressed
						x-small
						class="rounded-l-0"
						:color="order.bpt === -0.1 ? 'grey lighten-2' : null"
						@click="order.bpt = -0.1"
						><small>0.1</small></v-btn
					>
					<!-- <v-text-field v-model="order.bpt" label="Buy percent"></v-text-field> -->
				</v-col>
				<v-col cols="8" class="mt-0 pt-0 mb-0 pb-0">
					<div><small>sell percent :</small> {{ order.spt }}</div>
					<v-btn
						depressed
						small
						class="rounded-r-0"
						:color="order.spt === 1 ? 'grey lighten-2' : null"
						@click="order.spt = 1"
						>1</v-btn
					>
					<v-btn
						depressed
						small
						class="rounded-0"
						:color="order.spt === 1.5 ? 'grey lighten-2' : null"
						@click="order.spt = 1.5"
						>1.5</v-btn
					>
					<v-btn
						depressed
						small
						class="rounded-0"
						:color="order.spt === 2 ? 'grey lighten-2' : null"
						@click="order.spt = 2"
						>2</v-btn
					>
					<v-btn
						depressed
						small
						class="rounded-l-0"
						:color="order.spt === 3 ? 'grey lighten-2' : null"
						@click="order.spt = 3"
						>3</v-btn
					>
					<!-- <v-text-field v-model="order.spt" label="Sell percent"></v-text-field> -->
				</v-col>
			</v-row>

			<div class="mt-6">
				<small>id: {{ order.id }}</small>
			</div>
		</div>
	</v-card>
</template>

<script>
import HttpService from '@/services/HttpService';

export default {
	name: 'OrderTree',
	components: {},
	props: {
		order: {
			type: Object,
			default() {
				return undefined;
			},
		},
	},

	data: () => ({}),

	created() {
		// console.log(this.order)
	},

	methods: {
		async clickNew() {
			let dto = this.updateOrderToDto(this.order);
			this.$emit('on-updated-order', 'New', dto);
			// console.log(dto)
			const result = await HttpService.post('/coin/bybit/tribot/orders', dto);
			console.log(result);
		},

		async clickEdit() {
			let dto = this.updateOrderToDto(this.order);
			this.$emit('on-updated-order', 'Edit', dto);
			// console.log(dto)
			await HttpService.put('/coin/bybit/tribot/orders', dto);
		},

		async clickDelete() {
			let dto = this.updateOrderToDto(this.order);
			this.$emit('on-updated-order', 'Delete', dto);
			// console.log(dto)
			await HttpService.delete('/coin/bybit/tribot/cancel/orders/' + dto.id);
		},

		activeColor() {},

		updateOrderToDto(order) {
			order.buy_percent = Number(order.bpt);
			order.sell_percent = Number(order.spt);
			order.amt = Number(order.amt);

			order.price = order.buy_price * order.amt;
			order.price = this.toFixed(order.price, 2);

			return order;
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			};
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2);
			});

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length);
			});
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num));
		},
	},
};
</script>

