<template>
	<div>
		<v-card class="mx-auto mt-3" max-width="600">
			<div><v-icon small>mdi-pine-tree</v-icon>Tribot</div>
		</v-card>

		<v-card class="mx-auto" outlined rounded max-width="600">
			<GChart type="BubbleChart" :data="bubbleChartData" :options="bubbleChartOptions" />
		</v-card>

		<v-card class="mx-auto mt-5" max-width="600">
			<div><small>Daily Income</small></div>
		</v-card>
		<v-card class="mx-auto" outlined rounded max-width="600">
			<GChart type="LineChart" :data="lineChartData" :options="lineChartOptions" />

			<div class="mt-4 ml-2">
				<small>Avg Income: ${{ summaryObj.avgIncome }}</small>
			</div>

			<v-data-table
				class="mt-3"
				mobile-breakpoint="200"
				hide-default-footer
				:items-per-page="-1"
				:headers="history7dHeaders"
				:items="histories7Days"
				dense
			>
			</v-data-table>
		</v-card>

		<v-card class="mx-auto mt-5" max-width="600">
			<div><small>Trading log</small></div>
		</v-card>
		<v-card class="mx-auto" outlined rounded max-width="600">
			<apexchart type="scatter" height="260" :options="chartOptions" :series="series"></apexchart>

			<v-data-table
				class="mt-5"
				mobile-breakpoint="200"
				hide-default-footer
				:items-per-page="-1"
				:headers="history100Headers"
				:items="histories100"
				dense
			>
				<template v-slot:item.side="{ item }">
					<v-chip v-show="item.side === 'Buy'" class="pa-1" color="green lighten-4" x-small>{{
						item.side
					}}</v-chip>
					<v-chip v-show="item.side === 'Sell'" class="pa-1" color="red lighten-4" x-small>{{
						item.side
					}}</v-chip>
				</template>

				<template v-slot:item.shortSymbol="{ item }">
					<small>{{ item.shortSymbol }}</small>
				</template>
			</v-data-table>
		</v-card>
	</div>
</template>

<script>
import UserService from '@/services/UserService'
import HttpService from '@/services/HttpService'
import { GChart } from 'vue-google-charts'
import VueApexCharts from 'vue-apexcharts'

export default {
	components: {
		GChart,
		apexchart: VueApexCharts,
	},

	data: () => ({
		label: '',
		a: '',
		b: '',
		c: '',
		init_sum: '',

		selectedItem: 1,
		user: [],
		icon: 'mdi-flask-round-bottom',
		items: [
			{ text: 'Real-Time', icon: 'mdi-flask-round-bottom' },
			{ text: 'Audience', icon: 'mdi-flask-round-bottom' },
			{ text: 'Conversions', icon: 'mdi-flask-round-bottom' },
		],

		history7dHeaders: [
			{ text: 'Date', value: 'date' },
			{ text: 'Total', value: 'total' },
			{ text: 'Buy', value: 'buyTotal' },
			{ text: 'Sell', value: 'sellTotal' },
		],

		history100Headers: [
			{ text: '', value: 'shortSymbol' },
			{ text: 'Side', value: 'side' },
			{ text: 'Buy', value: 'buy_price' },
			{ text: 'Sell', value: 'sell_price' },
			{ text: 'date', value: 'newDate' },
		],

		histories100: [],
		histories7Days: [],

		summaryObj: {},

		lineChartData: [['Day', 'Buy', 'Sell', 'Total'],[0,0,0,0]],
		lineChartOptions: {
			chart: {
				title: 'Daily Income',
				subtitle: '7days',
			},
			// curveType: 'function',
			colors: ['#C8E6C9', '#FFCDD2', '#0D47A1'],
			legend: { position: 'bottom' },
			chartArea: {
				left: 45,
				right: 20,
			},
			hAxis: {
				direction: -1,
			},
		},

		bubbleChartData: [],
		bubbleChartOptions: {
			chart: {
				title: 'Daily Income',
				subtitle: '7days',
			},
			// curveType: 'function',
			colors: ['#C8E6C9', '#FFCDD2', '#0D47A1'],
			legend: { position: 'bottom' },
			chartArea: {
				left: 45,
				right: 20,
			},
			hAxis: { title: 'Trading Count', minValue: 0 },
			vAxis: { title: 'Profit', minValue: 0 },
			bubble: { textStyle: { fontSize: 9 } },
		},

		series: [],
		chartOptions: {
			chart: {
				height: 260,
				type: 'scatter',
				zoom: {
					enabled: false,
					// type: 'xy',
				},
			},
			colors: ['#81C784', '#E57373', '#9C27B0'],
			xaxis: {
				type: 'datetime',
				labels: {
					format: 'HH:mm',
				},
			},
			yaxis: {
				// tickAmount: 4,
			},
		},
	}),

	created: function () {
		this.telegramId = this.$store.state.telegramId
		this.init()
		this.get7dayHistories()
		this.get100Histories()
	},

	methods: {
		async init() {
			this.user = await UserService.getUser(this.telegramId)
		},

		async get7dayHistories() {
			const day = new Date().getDate()

			const LOADING_DAYS = 10

			let tempHistories = []
			let lineChartData = [['Day', 'Buy', 'Sell', 'Total']]

			let summaryObj = { avgIncome: 0 }

			for (let i = 0; i < LOADING_DAYS; i++) {
				let datedd = new Date().setDate(day - i)
				let dateYYMM = this.date2str(new Date(datedd), 'yyyy-MM-dd')
				let history = await HttpService.get('/coin/bybit/tribot/histories?date=' + dateYYMM)

				let shortDay = new Date(datedd).getDate()

				let tempHistory = { date: shortDay, total: 0, buyTotal: 0, sellTotal: 0 }
				for (let item in history) {
					tempHistory.buyTotal += history[item].bp
					tempHistory.sellTotal += history[item].sp
				}

				tempHistory.total = tempHistory.buyTotal + tempHistory.sellTotal

				summaryObj.avgIncome += tempHistory.total

				tempHistory.total = tempHistory.total.toFixed(1)
				tempHistory.buyTotal = tempHistory.buyTotal.toFixed(1)
				tempHistory.sellTotal = tempHistory.sellTotal.toFixed(1)
				tempHistories.push(tempHistory)

				lineChartData.push([
					'' + shortDay,
					Number(tempHistory.buyTotal),
					Number(tempHistory.sellTotal),
					Number(tempHistory.total),
				])
			}

			summaryObj.avgIncome = summaryObj.avgIncome / LOADING_DAYS
			summaryObj.avgIncome = summaryObj.avgIncome.toFixed(1)

			this.summaryObj = summaryObj
			this.lineChartData = lineChartData

			this.histories7Days = []
			for (const [index, item] of tempHistories.entries()) {
				if (index < 2) {
					this.histories7Days.push(item)
				}
			}
		},

		async get100Histories() {
			let histories = await HttpService.get('/coin/bybit/tribot/histories')
			let tempProfitSum = 0

			this.series = []
			this.histories100 = []
			this.bubbleChartData = [['symbol', 'Count', 'Total', 'Total', 'Total']]
			let buySeries = { name: 'Buy', data: [] }
			let sellSeries = { name: 'Sell', data: [] }

			let tempObj = {}

			for (let [index, item] of histories.entries()) {
				tempProfitSum += item.profit
				item.shortSymbol = item.symbol.split('USDT')[0]
				item.newDate = this.date2str(new Date(item.created_date), 'hh:mm')

				const gh = new Date(item.created_date).getHours()
				const krTime = new Date(item.created_date).setHours(gh + 9)

				if (item.side === 'Buy') {
					buySeries.data.push([new Date(krTime), item.profit])
				} else if (item.side === 'Sell') {
					sellSeries.data.push([new Date(krTime), item.profit])
				}

				if (tempObj[item.symbol] === undefined) {
					tempObj[item.symbol] = { shortSymbol: item.shortSymbol, count: 0, sum: 0 }
				}

				tempObj[item.symbol].count++
				tempObj[item.symbol].sum += item.profit

				if (index < 5) {
					this.histories100.push(item)
				}
			}

			let maxCount = 0
			let maxIncome = 0

			for (const item in tempObj) {
				this.bubbleChartData.push([
					tempObj[item].shortSymbol,
					tempObj[item].count,
					tempObj[item].sum,
					tempObj[item].sum,
					tempObj[item].sum,
				])

				if (tempObj[item].count > maxCount) {
					maxCount = tempObj[item].count
				}
				if (tempObj[item].sum > maxIncome) {
					maxIncome = tempObj[item].sum
				}
			}

			this.bubbleChartOptions.hAxis.maxValue = maxCount + 1
			this.bubbleChartOptions.vAxis.maxValue = maxIncome + 0.1

			this.series.push(buySeries)
			this.series.push(sellSeries)
			// this.histories100 = histories
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		clickBot(item) {
			if (item.bot_name === 'Tribot') {
				this.$router.push('/tribot')
			} else if (item.bot_name === 'Rangebot') {
				this.$router.push('/range')
			} else if (item.bot_name === 'Cscalper') {
				this.$router.push('/cscal')
			}
		},
	},
}
</script>
