<template>
	<div>
		<div v-if="user">
			<h3 class="mb-3">{{ user.bot_name }}</h3>

			{{ user.config }}
		</div>

		<v-row>
			<v-col cols="8" class="pr-0">
				<v-text-field
					v-model="configModel.amt"
					color="grey"
					label="Amount"
					outlined
					dense
					type="number"
				></v-text-field>
			</v-col>
			<v-col cols="4" class="">
				<v-btn depressed small color="grey lighten-2" @click="clickSaveAmt()"> save </v-btn>
			</v-col>
		</v-row>

		<!-- <div class="mt-2">Position</div>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="positionHeaders"
			:items="positions"
			dense
		>
		</v-data-table> -->

		<div class="mt-12">Strategy</div>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="strategyHeaders"
			:items="strategies"
			dense
		>
			<template v-slot:item.position="{ item }">
				<v-chip class="pa-1" :color="sideColor(item.position)" x-small>{{ item.position }}</v-chip>
			</template>
		</v-data-table>
	</div>
</template>

<script>
import HttpService from '@/services/HttpService'
import UserService from '@/services/UserService'

export default {
	name: 'Mulbot',

	components: {},

	data: () => ({
		// positionHeaders: [
		// 	{ text: '', value: 'symbol' },
		// 	{ text: 'Position', value: 'position' },
		// 	{ text: 'Price', value: 'price' },
		// 	{ text: 'Amt', value: 'amt' },
		// 	{ text: 'Contracts', value: 'contracts' },
		// 	{ text: 'Size', value: 'size' },
		// 	{ text: 'Frame', value: 'frame' },
		// 	{ text: 'Status', value: 'order_status' },
		// 	{ text: 'Date', value: 'date' },
		// ],

		strategyHeaders: [
			{ text: '', value: 'symbol' },
			{ text: 'Position', value: 'position' },
			{ text: 'Status', value: 'order_status' },
			{ text: 'Amt', value: 'amt' },
			{ text: 'Price', value: 'price' },
			{ text: 'Contracts', value: 'contracts' },
			{ text: 'Size', value: 'size' },
			{ text: 'Frame', value: 'frame' },
			{ text: 'Action', value: 'action' },
			{ text: 'Date', value: 'date', width: '95' },
		],

		strategies: [],
		positions: [],
		configModel: {},
		user: {},
	}),

	computed: {
		// formTitle () {
		//   return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
		// },
	},

	watch: {
		// editedItem: {
		//     deep: true,
		//     handler(){
		//         this.editedItem.profit = ((this.editedItem.sell_price - this.editedItem.buy_price) / this.editedItem.buy_price * 100).toFixed(2) + '%'
		//     }
		// }
	},

	created() {
		this.init()
	},

	methods: {
		async init() {
			console.log(this.$route.query.bot_name)

			await this.getUser()
			// await this.getStrategies()
			await this.getPositions()
		},

		async getPositions() {
			let strategies = await this.getStrategies()
			const uri = `/coin/bot/rangebot/positions?telegram_id=${this.$store.state.telegramId}&bot_name=${this.user.bot_name}`
			let positions = await this.requestGet(process.env.VUE_APP_API + uri)

			this.positions = positions
			// console.log(positions)

			let tempList = []

			for (let item of strategies) {
				// console.log(item.id)

				if (item.frame === this.user.frame) {
					let findItem = this.$underscore.find(positions, {
						tradingview_id: item.id,
					})
					if (findItem) {
						item.amt = findItem.amt
						item.position = findItem.position.replace('_Switching', '(S)')
						item.price = findItem.price
						item.order_status = findItem.order_status
						// console.log(findItem)
					}
					item.date = this.date2str(new Date(item.created_date), 'MM-dd hh:mm')
					tempList.push(item)
				}
			}

			this.strategies = tempList
		},

		async getStrategies() {
			let indicator = ''

			if (this.user.bot_name === 'Rangebot') {
				indicator = 'RANGEFILTER'
			} else if (this.user.bot_name === 'Cscalper') {
				indicator = 'CRYPTOSCALPER'
			} else if (this.user.bot_name === 'MacdEma') {
				indicator = 'MacdEma'
			}

			const uri = `/coin/bot/rangebot/orders?indicator=${indicator}`
			let strategies = await this.requestGet(process.env.VUE_APP_API + uri)
			for (let item of strategies) {
				item.date = this.date2str(new Date(item.created_date), 'MM-dd hh:mm')
			}
			return strategies
			// console.log(strategies)
		},

		async getUser() {
			const users = await UserService.getUser(this.$store.state.telegramId)
			for (const item of users) {
				if (
					item.bot_name === this.$route.query.bot_name &&
					item.exchange === this.$route.query.exchange &&
					item.frame === this.$route.query.frame
				) {
					item.config = JSON.parse(item.config)
					this.user = item
				}
			}
		},

		sideColor(side) {
			let color = ''
			if (side === 'LongOpen') {
				color = 'green lighten-4'
			} else if (side === 'LongOpen(S)') {
				color = 'green lighten-4'
			} else if (side === 'ShortOpen') {
				color = 'red lighten-4'
			} else if (side === 'ShortOpen(S)') {
				color = 'red lighten-4'
			} else if (side === 'ShortClose') {
				color = 'grey lighten-2'
			} else if (side === 'LongClose') {
				color = 'grey lighten-2'
			} else {
				color = 'white'
			}
			return color
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}

			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		requestGet(uri) {
			// localStorage.setItem('mexRequestModel', JSON.stringify(this.mexRequestModel));
			// let header = { headers: { sessionKey: this.mexRequestModel.key } }

			return new Promise((resolve, reject) => {
				this.$http
					.get(uri)
					.then((result) => {
						resolve(result.data)
					})
					.catch((error) => {
						resolve()
					})
			})
		},

		async clickSaveAmt() {
			if (this.configModel.amt) {
				if (this.user.config === null) {
					this.user.config = this.configModel
				} else {
					if (this.user.config.amt !== undefined) {
						this.user.config.amt = this.configModel.amt
					}
				}
				const putResult = await HttpService.put('/pish/telegram/users/' + this.user.telegram_id, this.user)
			}
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>
