<template>
	<div>
		<!-- <div>
			<v-btn
				small
				depressed
				color="grey lighten-4"
				class="ml-5"
				@click="
					showHistory = true
					showOrder = false
				"
				>Histories
			</v-btn>
		</div> -->

		<!-- <slider-view ref="sliderView"></slider-view> -->
		<!-- <apex-bubble ref="apexBubbleView"></apex-bubble> -->

		<v-card>
			<div>
				<small>Total : {{ summary.totalIncome }}</small>
			</div>
			<div>
				<small>PERP : {{ summary.totalIncomePerp }}</small
				>, <small>USDT : {{ summary.totalIncomeUsdt }}</small
				>,<small> BUSD : {{ summary.totalIncomeBusd }}</small>
			</div>
		</v-card>

		<div>
			<v-data-table
				mobile-breakpoint="200"
				hide-default-footer
				:items-per-page="-1"
				group-by="sg"
				:headers="orderHeaders"
				:items="orders"
				:sort-by="['sg', 'buyPrice']"
				:sort-desc="[false, true]"
				@click:row="clickTableRow"
				must-sort
				dense
			>
				<template v-slot:item.symbol="{ item }">
					<small>{{ item.symbol }}</small>
				</template>

				<template v-slot:item.position="{ item }">
					<v-chip class="pa-1" :color="positionColor(item.position)" x-small>{{ item.position }}</v-chip>
				</template>

				<template v-slot:item.percent="{ item }">
					<v-chip class="pa-1" :color="percentColor(item)" x-small>{{ item.percent }}</v-chip>
				</template>

				<template v-slot:item.amt="{ item }">
					<small>{{ item.amt }}</small>
				</template>

				<template v-slot:item.status="{ item }">
					<small>{{ item.status }}</small>
				</template>
				<template v-slot:item.buyPrice="{ item }">
					<small>{{ item.buyPrice }}</small>
				</template>
				<template v-slot:item.markPrice="{ item }">
					<small>{{ item.markPrice }}</small>
				</template>
				<template v-slot:item.sell_percent="{ item }">
					<small>{{ item.sell_percent }}</small>
				</template>
			</v-data-table>
		</div>

		<v-dialog v-model="showDialog" width="500">
			<v-card>
				<v-card-title dense class="grey lighten-2"> New </v-card-title>

				<v-row class="mt-5 ml-5 mr-5">
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<small>{{ selectedItem.symbol }} </small>
					</v-col>
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-btn
							depressed
							x-small
							class="rounded-r-0"
							:color="selectedItem.position === 'LONG' ? 'green lighten-3' : null"
							@click="selectedItem.position = 'LONG'"
							><small>LONG</small></v-btn
						>
						<v-btn
							depressed
							x-small
							class="rounded-l-0"
							:color="selectedItem.position === 'SHORT' ? 'red lighten-3' : null"
							@click="selectedItem.position = 'SHORT'"
							><small>SHORT</small></v-btn
						>
					</v-col>
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-text-field v-model="selectedItem.amt" label="amt"></v-text-field>
					</v-col>
				</v-row>

				<v-row class="mt-5 ml-5 mr-5">
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-text-field v-model="selectedItem.sell_percent" label="sell_percent"></v-text-field>
					</v-col>
				</v-row>

				<v-divider></v-divider>

				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn
						class="ma-2 mr-5"
						depressed
						color="red lighten-2"
						small
						@click="clickDeleteOrder(selectedItem)"
					>
						Delete
					</v-btn>
					<v-btn
						class="ma-2"
						depressed
						color="primary lighten-1"
						small
						@click="clickAddNewOrder(selectedItem)"
					>
						Add new order
					</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</div>
</template>

<script>
import Util from '../../../Util'
import HttpService from '@/services/HttpService'
// import SliderView from './SliderView'
// import ApexBubble from './ApexBubble'

export default {
	name: 'Mulbot',

	components: {
		// SliderView,
		// ApexBubble,
	},

	data: () => ({
		showDialog: false,

		orderHeaders: [
			{ text: '', value: 'sg' },
			{ text: 'symbol', value: 'symbol' },
			{ text: 'position', value: 'position' },
			{ text: 'percent', value: 'percent' },
			{ text: 'status', value: 'status' },
			{ text: 'price', value: 'price' },
			{ text: 'amt', value: 'amt' },
			{ text: 'buyPrice', value: 'buyPrice' },
			{ text: 'markPrice', value: 'markPrice' },
			{ text: 'sellPercent', value: 'sell_percent' },
		],

		selectedItem: {},
		summary: { totalIncomeBusd: 0, totalIncomeUsdt: 0, totalIncomePerp: 0 },

		orders: [],
		symbolSummary: {},
	}),

	computed: {},

	watch: {},

	created() {
		// this.initialize()
		this.initWorker()
	},

	methods: {
		async initWorker() {
			await this.getHistories()
			await this.getOrderList()

			// this.$refs.sliderView.createChartData(this.orders, this.symbolSummary)
			// this.$refs.apexBubbleView.createBubbleChartData(this.orders, this.symbolSummary)
		},

		async getHistories() {
			let totalIncomeBusd = 0
			let totalIncomeUsdt = 0
			let totalIncomePerp = 0

			let histories = await HttpService.get('/coin/binance/tribot/histories')
			// console.log(histories)

			for (let item of histories) {
				let lastChars = item.symbol.substr(-4)
				if (lastChars === 'BUSD') {
					let earn = 0
					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					}

					totalIncomeBusd += earn
				} else if (lastChars === 'USDT') {
					let earn = 0
					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					}

					totalIncomeUsdt += earn
				} else if (lastChars === 'PERP') {
					let earn = 0
					let convertedUsd = 0
					if (item.symbol === 'BTCUSD_PERP') {
						convertedUsd = 100
					} else {
						convertedUsd = 10
					}

					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = convertedUsd * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = convertedUsd * percent * 0.01
					}

					totalIncomePerp += earn
				}
			}

			this.summary.totalIncomeUsdt = Number(totalIncomeUsdt.toFixed(1))
			this.summary.totalIncomeBusd = Number(totalIncomeBusd.toFixed(1))
			this.summary.totalIncomePerp = Number(totalIncomePerp.toFixed(1))
			this.summary.totalIncome = (totalIncomeUsdt + totalIncomeBusd + totalIncomePerp).toFixed(1)
		},

		async getOrderList() {
			let orders = await HttpService.get('/coin/binance/tribot/orders')
			let positions = await HttpService.get('/coin/binance/tribot/positions')
			let positionsRisk = await HttpService.get('/coin/binance/tribot/positions/risk')

			// console.log(positionsRisk)

			for (let item of orders) {
				if (this.symbolSummary[item.symbol] === undefined) {
					this.symbolSummary[item.symbol] = {
						minPrice: 999999,
						maxPrice: 0,
						nowPrice: 0,
						name: item.symbol,
						data: [],
						data2: {},
					}
				}
			}

			for (let item of orders) {
				let position = this.$underscore.find(positions, { tribot_order_id: item.id })
				let positionRisk = this.$underscore.find(positionsRisk, { symbol: item.symbol })

				if (item.symbol.includes('BTC')) {
					item.sg = '01BTC'
				} else if (item.symbol.includes('ETH')) {
					item.sg = '02ETH'
				} else if (item.symbol.includes('BNB')) {
					item.sg = '05BNB'
				} else if (item.symbol.includes('XRP')) {
					item.sg = '06XRP'
				} else if (item.symbol.includes('ADA')) {
					item.sg = '08ADA'
				} else if (item.symbol.includes('DOT')) {
					item.sg = '11DOT'
				} else if (item.symbol.includes('TRX')) {
					item.sg = '15TRX'
				} else if (item.symbol.includes('LTC')) {
					item.sg = '18LTC'
				} else if (item.symbol.includes('MATIC')) {
					item.sg = '21MATIC'
				} else if (item.symbol.includes('BCH')) {
					item.sg = '24BCH'
				} else if (item.symbol.includes('LINK')) {
					item.sg = '25LINK'
				} else if (item.symbol.includes('NEAR')) {
					item.sg = '23NEAR'
				} else if (item.symbol.includes('XLM')) {
					item.sg = '26XLM'
				} else if (item.symbol.includes('ALGO')) {
					item.sg = '28ALGO'
				} else if (item.symbol.includes('ATOM')) {
					item.sg = '29ATOM'
				} else if (item.symbol.includes('XMR')) {
					item.sg = '31XMR'
				} else if (item.symbol.includes('ETC')) {
					item.sg = '32ETC'
				} else if (item.symbol.includes('HBAR')) {
					item.sg = '35HBAR'
				} else if (item.symbol.includes('DOGE')) {
					item.sg = '10DOGE'
				} else if (item.symbol.includes('MKR')) {
					item.sg = '53MKR'
				} else if (item.symbol.includes('AVAX')) {
					item.sg = '14AVAX'
				} else if (item.symbol.includes('EGLD')) {
					item.sg = '36EGLD'
				} else if (item.symbol.includes('XTZ')) {
					item.sg = '46XTZ'
				} else if (item.symbol.includes('DASH')) {
					item.sg = '91DASH'
				} else if (item.symbol.includes('IOTA')) {
					item.sg = '66IOTA'
				} else if (item.symbol.includes('VET')) {
					item.sg = '37VET'
				} else if (item.symbol.includes('SC')) {
					item.sg = '163SC'
				} else if (item.symbol.includes('1INCH')) {
					item.sg = 'z124INCH'
				} else if (item.symbol.includes('OMG')) {
					item.sg = 'z129OMG'
				} else if (item.symbol.includes('ZEN')) {
					item.sg = 'z168ZEN'
				}

				if (position !== undefined) {
					item.buyPrice = position.buy_price

					if (position.sell_order_code === null) {
						item.status = position.buy_order_status
					} else {
						item.status = position.sell_order_code
					}

					this.symbolSummary[item.symbol].status = item.status
					this.symbolSummary[item.symbol].position = item.position
				} else {
					item.status = '-'
				}

				if (positionRisk) {
					let markPrice = Number(positionRisk.markPrice)

					if (this.symbolSummary[item.symbol].minPrice > item.buyPrice) {
						this.symbolSummary[item.symbol].minPrice = item.buyPrice
					}

					if (this.symbolSummary[item.symbol].maxPrice < item.buyPrice) {
						this.symbolSummary[item.symbol].maxPrice = item.buyPrice
					}

					if (this.symbolSummary[item.symbol].minPrice > markPrice) {
						this.symbolSummary[item.symbol].minPrice = markPrice
					}

					this.symbolSummary[item.symbol].nowPrice = markPrice

					if (item.position === 'LONG') {
						let positionPercent = (markPrice / item.buyPrice - 1) * 100
						item.percent = positionPercent
					} else if (item.position === 'SHORT') {
						let positionPercent = (item.buyPrice / markPrice - 1) * 100
						item.percent = positionPercent
					}

					item.percent = item.percent.toFixed(1)
					if (isNaN(item.percent)) {
						item.percent = ''
					}

					this.symbolSummary[item.symbol].percent = item.percent

					if (item.symbol.includes('BTC')) {
						item.markPrice = markPrice.toFixed(0)
					} else if (item.symbol.includes('ETH')) {
						item.markPrice = markPrice.toFixed(0)
					} else if (item.symbol.includes('BNB')) {
						item.markPrice = markPrice.toFixed(1)
					} else if (item.symbol.includes('XRP')) {
						item.markPrice = markPrice.toFixed(3)
					} else if (item.symbol.includes('BCH')) {
						item.markPrice = markPrice.toFixed(1)
					} else if (item.symbol.includes('MKR')) {
						item.markPrice = markPrice.toFixed(0)
					}

					let convertedAmt = item.amt

					if (item.symbol === 'BTCUSD_PERP') {
						convertedAmt = 0.0034
					} else if (item.symbol === 'ETHUSD_PERP') {
						convertedAmt = 0.005
					} else if (item.symbol === 'BNBUSD_PERP') {
						convertedAmt = 0.0338
					} else if (item.symbol === 'XRPUSD_PERP') {
						convertedAmt = 24
					} else if (item.symbol === 'ADAUSD_PERP') {
						convertedAmt = 18.7
					} else if (item.symbol === 'DOTUSD_PERP') {
						convertedAmt = 0.992
					} else if (item.symbol === 'TRXUSD_PERP') {
						convertedAmt = 143.3
					} else if (item.symbol === 'LTCUSD_PERP') {
						convertedAmt = 0.145
					} else if (item.symbol === 'MATICUSD_PERP') {
						convertedAmt = 14.89
					}

					item.price = (convertedAmt * markPrice).toFixed(1)
				}
			}
			this.orders = orders
		},

		clickTableRow(item) {
			console.log('clickTableRow')
			this.showDialog = true
			this.selectedItem = item
			console.log(item)
		},

		async clickAddNewOrder(item) {
			// console.log(item)
			if (item) {
				const result = await HttpService.post('/coin/binance/tribot/orders', item)
				this.getOrderList()
				this.showDialog = false
			}
		},

		async clickDeleteOrder(item) {
			if (item.status !== 'FILLED') {
				// console.log('can delete')
				await HttpService.delete('/coin/binance/tribot/orders/' + item.id)
				this.getOrderList()
				this.showDialog = false
			}
		},

		priceColor(item) {
			let color = ''
			if (item === 'Created') {
				color = 'amber lighten-4'
			} else if (item === 'New') {
				color = 'amber lighten-2'
			} else if (item === 'Filled') {
				color = 'blue lighten-3'
			} else if (item === 'PartiallyFilled') {
				color = 'blue lighten-4'
			} else if (item === 'Cancelled') {
				color = 'red lighten-2'
			} else if (item === 'Ticker') {
				color = 'transparent'
			} else {
				color = 'transparent'
			}
			return color
		},

		positionColor(item) {
			let color = ''
			if (item === 'LONG') {
				color = 'green lighten-5'
			} else if (item === 'SHORT') {
				color = 'red lighten-5'
			}
			return color
		},

		percentColor(item) {
			let color = 'white lighten-3'
			if (item.percent > item.sell_percent) {
				color = 'green lighten-2'
			} else if (item.percent > 0.5) {
				color = 'green lighten-3'
			} else if (item.percent > 0.3) {
				color = 'green lighten-4'
			} else if (item.percent > 0) {
				color = 'green lighten-5'
			} else if (item.percent < -1) {
				color = 'red lighten-5'
			} else if (item.percent > -1 && item.percent < 0) {
				color = 'red lighten-4'
			}
			return color
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>

