<template>
	<v-card outlined>
		<v-data-table
			mobile-breakpoint="200"
			hide-default-footer
			:items-per-page="-1"
			:headers="orderHeaders"
			:items="orders"
			:sort-by="['buy_price']"
			:sort-desc="[true]"
			:expanded.sync="expanded"
			@click:row="clickOrderRow"
			dense
		>
			<template v-slot:expanded-item="{ headers, item }">
				<td class="pt-3" :colspan="headers.length">
					<order-edit @on-updated-order="onUpdatedOrder" :order="item"></order-edit>
				</td>
			</template>

			<template v-slot:item.shortSymbol="{ item }">
				<small>{{ item.shortSymbol }}</small>
			</template>

			<template v-slot:item.side="{ item }">
				<v-chip class="pa-1" :color="sideColor(item.side)" x-small>{{ item.side }}</v-chip>
			</template>

			<template v-slot:item.buy_price="{ item }">
				<div class="">
					<small class="text--secondary">{{ item.bpt }} </small>
					<v-chip class="pa-1" :color="priceColor(item.bs)" x-small></v-chip>
					{{ item.buy_price }}
				</div>
			</template>

			<template v-slot:item.sell_price="{ item }">
				<div class="">
					<small class="text--secondary">{{ item.spt }} </small>
					<v-chip class="pa-1" :color="priceColor(item.ss)" x-small></v-chip>
					{{ item.sell_price }}
				</div>
			</template>
		</v-data-table>
	</v-card>
</template>

<script>
// import HttpService from '@/services/HttpService'

import OrderEdit from './OrderEdit'

export default {
	name: 'OrderTree',
	components: { OrderEdit },
	props: {
		orders: {
			type: Array,
			default() {
				return []
			},
		},
	},

	data: () => ({
		orderHeaders: [
			{ text: '', value: 'shortSymbol', width: 40 },
			{ text: 'Side', value: 'side', width: 30 },
			{
				text: 'Buy',
				value: 'buy_price',
				width: 102,
			},
			{
				text: 'Sell',
				value: 'sell_price',
				width: 102,
			},
			{ text: 'P', value: 'price' },
		],

		expanded: [],
	}),

	computed: {},
	watch: {},
	created() {},
	methods: {
		// setOrders(orders) {
		// 	this.orders = orders
		// },

		priceColor(item) {
			let color = ''
			if (item === 'Created') {
				color = 'amber lighten-4'
			} else if (item === 'New') {
				color = 'amber lighten-2'
			} else if (item === 'Filled') {
				color = 'blue lighten-3'
			} else if (item === 'PartiallyFilled') {
				color = 'blue lighten-4'
			} else if (item === 'Cancelled') {
				color = 'red lighten-2'
			} else if (item === 'Ticker') {
				color = 'transparent'
			} else {
				color = 'transparent'
			}
			return color
		},

		sideColor(side) {
			let color = ''
			if (side === 'Buy') {
				color = 'green lighten-4'
			} else if (side === 'Sell') {
				color = 'red lighten-4'
			} else {
				color = 'white'
			}
			return color
		},

		onUpdatedOrder(type, order) {
			// console.log('onUpdatedOrder in OrderTree')
			// console.log(type)
			// console.log(order)

			if(type === 'New'){
				// this.orders.push(order)
			}

			// console.log(this.orders)
			this.$emit('on-updated-order', type, order)

			this.expanded = []
		},

		clickOrderRow(row) {
			// console.log(row)
			if (this.expanded.length && row.id === this.expanded[0].id) {
				this.expanded = []
			} else {
				this.expanded = [row]
			}
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>

