<template>
	<div>
		<v-card class="mx-auto mt-5" max-width="620">
			<div>Hive Voting </div>
		</v-card>

		<v-card outlined class="mx-auto rounded-lg" max-width="620">
			<v-text-field
				class="mt-5 ml-3 mr-3"
				v-model="postUrl"
				hint="Please enter the full url of the post."
				label="Post Url"
			></v-text-field>
			<div class="d-flex flex-row-reverse mt-2 mr-3 mb-5">
				<v-btn small depressed color="grey lighten-4" @click="clickSubmit()">submit </v-btn>
			</div>
		</v-card>

		<v-snackbar class="mb-12" v-model="showSnackbar" absolute bottom color="primary" center text>
			The post url is saved.
		</v-snackbar>

		<v-card class="mx-auto mt-10" max-width="620">
			<div>Voting List</div>
		</v-card>
		<v-card outlined class="mx-auto rounded-lg" max-width="620">
			<v-data-table
				mobile-breakpoint="200"
				hide-default-footer
				:items-per-page="-1"
				:headers="votingHeaders"
				:items="votingList"
				dense
			>
				<template v-slot:item.url="{ item }">
					<a :href="item.url" target="new"> {{ item.url }} </a>
				</template>
				<template v-slot:item.voted="{ item }">
					<v-icon v-if="item.voted === 1" small color="green lighten-2">
						mdi-checkbox-marked-circle-outline
					</v-icon>
				</template>
			</v-data-table>
		</v-card>
	</div>
</template>

<script>
import HttpService from '@/services/HttpService'

export default {
	components: {},

	data: () => ({
		votingHeaders: [
			{ text: 'URL', value: 'url' },
			{ text: 'Voted', value: 'voted' },
			{ text: 'Date', value: 'newDate' },
		],
		votingList: [],
		postUrl: null,
		showSnackbar: false,
	}),

	created: function () {
		this.init()
	},

	methods: {
		async init() {
			let votingList = await HttpService.get('/pish/hive/votings')
			for (let item of votingList) {
				item.newDate = this.date2str(new Date(item.created_date), 'yyyy-MM-dd hh:mm')
			}
			this.votingList = votingList
		},

		async clickSubmit() {
			let model = { url: this.postUrl }
			await HttpService.post('/pish/hive/votings', model)
			this.init()
			this.postUrl = null
			this.showSnackbar = true
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},
	},
}
</script>
