    testPriceMain(rounds) {
      this.summary.priceMainWinCount = 0;
      this.summary.priceMainCount = 0;

      this.summary.followPlainSpotWinCount = 0;
      this.summary.followPlainSpotCount = 0;

      for (let i = rounds.length - 3; i >= 0; i--) {
        // plain start
        rounds[i].followPlainSpot = rounds[i].position;
        if (rounds[i + 1].bnb_usdt < rounds[i].bnb_usdt) {
          rounds[i].followPlainSpot = 'UP';
        } else {
          rounds[i].followPlainSpot = 'DOWN';
        }
        if (rounds[i].bnb_usdt <= 0) {
          rounds[i].followPlainSpot = '0';
        }

        if (rounds[i].bnb_usdt === rounds[i + 1].bnb_usdt) {
          rounds[i].followPlainSpot = 'same_price';
        }

        if (rounds[i].followPlainSpot === 'UP' || rounds[i].followPlainSpot === 'DOWN') {
          this.summary.followPlainSpotCount++;
        }
        if (rounds[i].up_down === rounds[i].followPlainSpot) {
          this.summary.followPlainSpotWinCount++;
        }
        // plain end

        rounds[i].priceMain = rounds[i].position;

        if (rounds[i + 1].bnb_usdt < rounds[i].bnb_usdt) {
          rounds[i].priceMain = 'UP';
        } else {
          rounds[i].priceMain = 'DOWN';
        }

        if (!rounds[i].bnb_usdt) {
          rounds[i].priceMain = '0';
        }

        // rsi over 70
        if (this.tradeSpotOptions.rsi_up_over > 0) {
          if (rounds[i].rsi > this.tradeSpotOptions.rsi_up_over) {
            if (rounds[i].priceMain === 'UP') {
              rounds[i].priceMain = 'rsi_up_over';
            }
          }
        }

        if (this.tradeSpotOptions.rsi_down_over > 0) {
          if (rounds[i].rsi < this.tradeSpotOptions.rsi_down_over) {
            if (rounds[i].priceMain === 'DOWN') {
              rounds[i].priceMain = 'rsi_down_over';
            }
          }
        }

        if (this.tradeSpotOptions.rsi_up_follow > 0) {
          if (rounds[i].rsi > this.tradeSpotOptions.rsi_up_follow) {
            if (rounds[i].priceMain === 'DOWN') {
              rounds[i].priceMain = 'rsi_up_follow';
            }
          }
        }

        if (this.tradeSpotOptions.rsi_down_follow > 0) {
          if (rounds[i].rsi < this.tradeSpotOptions.rsi_down_follow) {
            if (rounds[i].priceMain === 'UP') {
              rounds[i].priceMain = 'rsi_down_follow';
            }
          }
        }

        // check ema side
        if (this.tradeSpotOptions.ema1_ema2_side > 0) {
          if (rounds[i].ema1 > rounds[i].ema2) {
            if (rounds[i].priceMain === 'DOWN') {
              rounds[i].priceMain = 'ema1_ema2_side';
            }
          } else {
            if (rounds[i].priceMain === 'UP') {
              rounds[i].priceMain = 'ema1_ema2_side';
            }
          }
        }

        // ema1_price_side
        if (this.tradeSpotOptions.ema1_price_side > 0) {
          if (rounds[i].ema1 < rounds[i].bnb_usdt) {
            if (rounds[i].priceMain === 'DOWN') {
              rounds[i].priceMain = 'ema1_price_side';
            }
          } else {
            if (rounds[i].priceMain === 'UP') {
              rounds[i].priceMain = 'ema1_price_side';
            }
          }
        }

        // check over heating
        if (this.tradeSpotOptions.over_heating > 0) {
          const priceAbs = Math.abs(rounds[i].bnb_usdt - rounds[i + 1].bnb_usdt);
          if (priceAbs > rounds[i].bnb_usdt * this.tradeSpotOptions.over_heating) {
            rounds[i].priceMain = 'over_heating';
          }
        }

        // same price
        if (this.tradeSpotOptions.same_price > 0) {
          if (rounds[i].bnb_usdt === rounds[i + 1].bnb_usdt) {
            rounds[i].priceMain = 'same_price';
          }
        }

        // narrow price
        // if (priceAbs <= 0.1) {
        //   rounds[i].priceMain = 'price narrow';
        // }

        // check narrow ema
        // const emaAbs = Math.abs(rounds[i].ema1 - rounds[i].bnb_usdt);
        // if (emaAbs < rounds[i].ema1 * 0.0006) {
        //   rounds[i].priceMain = 'ema price narrow';
        // }

        if (rounds[i].priceMain === 'UP' || rounds[i].priceMain === 'DOWN') {
          this.summary.priceMainCount++;
        }

        if (rounds[i].up_down === rounds[i].priceMain) {
          this.summary.priceMainWinCount++;
        }
      }
      return rounds;
    },

    testPriceFutures(rounds) {
      this.summary.priceFuturesWinCount = 0;
      this.summary.priceFuturesCount = 0;

      this.summary.followPlainFuturesWinCount = 0;
      this.summary.followPlainFuturesCount = 0;

      for (let i = rounds.length - 3; i >= 0; i--) {
        // plain start
        rounds[i].followPlainFutures = rounds[i].position;
        if (rounds[i + 1].bnb_usdt_f < rounds[i].bnb_usdt_f) {
          rounds[i].followPlainFutures = 'UP';
        } else {
          rounds[i].followPlainFutures = 'DOWN';
        }
        if (rounds[i].bnb_usdt_f <= 0) {
          rounds[i].followPlainFutures = '0';
        }
        if (rounds[i].bnb_usdt_f === rounds[i + 1].bnb_usdt_f) {
          rounds[i].followPlainFutures = 'same_price';
        }

        const priceAbs = Math.abs(rounds[i].bnb_usdt_f - rounds[i + 1].bnb_usdt_f);
        if (priceAbs <= this.tradeFutureOptions.narrow_price) {
          rounds[i].followPlainFutures = 'narrow_price';
        }

        if (rounds[i].followPlainFutures === 'UP' || rounds[i].followPlainFutures === 'DOWN') {
          this.summary.followPlainFuturesCount++;
        }
        if (rounds[i].up_down === rounds[i].followPlainFutures) {
          this.summary.followPlainFuturesWinCount++;
        }

        //plain end

        if (rounds[i + 2].priceFutures === 'DOWN') {
          rounds[i].priceFutures = 'UP';
        } else {
          rounds[i].priceFutures = 'DOWN';
        }

        if (rounds[i].priceFutures === 'UP' || rounds[i].priceFutures === 'DOWN') {
          this.summary.priceFuturesCount++;
        }

        if (rounds[i].up_down === rounds[i].priceFutures) {
          this.summary.priceFuturesWinCount++;
        }
      }

      // console.log(this.enters);

      return rounds;
    },

    testPlainPriceFuturesCal(rounds) {
      let interval = 2;
      // const targetPosition = 'followPlainFutures';
      const targetPosition = 'priceFutures';
      const targetPositionIndex = 'priceFuturesIndex';
      const targetPositionStatus = 'priceFuturesStatus';

      for (let i = rounds.length - interval - 3; i >= 0; i--) {
        console.log(rounds[i].up_down, rounds[i][targetPosition]);
        if (rounds[i + 2].up_down === rounds[i + 2][targetPosition]) {
          rounds[i].priceFuturesIndex = 1;
        } else {
          rounds[i].priceFuturesIndex = rounds[i + 2].priceFuturesIndex + 1;
        }
      }

      this.enters = [
        {
          i1: { win: 0, lose: 0 },
          i2: { win: 0, lose: 0 },
          i3: { win: 0, lose: 0 },
          i4: { win: 0, lose: 0 },
          i5: { win: 0, lose: 0 },
          i6: { win: 0, lose: 0 },
          i7: { win: 0, lose: 0 },
          i8: { win: 0, lose: 0 },
          i9: { win: 0, lose: 0 },
          i10: { win: 0, lose: 0 },
          i11: { win: 0, lose: 0 },
        },
      ];

      for (let i = rounds.length - interval - 1; i >= 0; i--) {
        if (rounds[i][targetPositionIndex]) {
          if (rounds[i].up_down === rounds[i][targetPosition]) {
            // console.log(this.enters[0]['i' + rounds[i][targetPositionIndex]]);
            this.enters[0]['i' + rounds[i][targetPositionIndex]].win++;
          } else {
            // console.log(rounds[i][targetPositionIndex]);
            // console.log(this.enters[0]['i' + rounds[i][targetPositionIndex]]);
            this.enters[0]['i' + rounds[i][targetPositionIndex]].lose++;
          }
        }
      }
    },

    testFollowPlainLink(rounds) {
      this.summary.followPlainWinCount = 0;
      this.summary.followPlainCount = 0;

      for (let i = rounds.length - 3; i >= 0; i--) {
        rounds[i].followPlain = rounds[i].position;

        if (rounds[i + 1].bnb_link < rounds[i].bnb_link) {
          rounds[i].followPlain = 'UP';
        } else {
          rounds[i].followPlain = 'DOWN';
        }

        if (
          Math.abs(rounds[i].bnb_link - rounds[i + 1].price) <= this.tradeFutureOptions.narrow_price
        ) {
          rounds[i].followPlain = 'narrow_price';
        }

        if (rounds[i].followPlain === 'UP' || rounds[i].followPlain === 'DOWN') {
          this.summary.followPlainCount++;
        }

        if (rounds[i].up_down === rounds[i].followPlain) {
          this.summary.followPlainWinCount++;
        }
      }
      return rounds;
    },

    getFrame(rounds, start, size) {
      let list = [];
      for (let i = start; i < start + size; i++) {
        list.push(rounds[i]);
      }
      return list;
    },

