    async clickApplyTest(nameNum, amtTest) {
      let numList = amtTest.map((x) => Number(x));
      let name = '';
      if (nameNum === 1) {
        name = 'index1_test1';
      } else if (nameNum === 2) {
        name = 'index1_test2';
      } else if (nameNum === 3) {
        name = 'index1_reverse_test1';
      }

      await HttpService.put('/pish/prediction/amts', { name: name, amt_list: numList });
      this.setAmts();
    },

    getPercent(total, count) {
      return ((count / total) * 100).toFixed(1);
    },

    winOrLoseColor(up_down, position, index) {
      let color = 'white lighten-3';

      if (position === 'UP' || position === 'DOWN') {
        if (up_down === position) {
          color = 'green lighten-4';
        } else {
          color = 'red lighten-4';
        }
      }

      return color;
    },

    winOrLoseColorByStatus(up_down, position, status) {
      let color = 'white lighten-3';

      if (status === 'enter' && (position === 'UP' || position === 'DOWN')) {
        if (up_down === position) {
          color = 'green lighten-4';
        } else {
          color = 'red lighten-4';
        }
      }

      return color;
    },

    winColor(up_down, position, index) {
      let color = 'white lighten-3';

      if (up_down === position) {
        if (position === 'UP') {
          if (index >= 7) {
            color = 'green lighten-1';
          } else if (index >= 6) {
            color = 'green lighten-2';
          } else if (index >= 5) {
            color = 'green lighten-3';
          } else if (index >= 3) {
            color = 'green lighten-4';
          } else if (index >= 1) {
            color = 'green lighten-4';
          }
        } else if (position === 'DOWN') {
          if (index >= 7) {
            color = 'red lighten-1';
          } else if (index >= 6) {
            color = 'red lighten-2';
          } else if (index >= 5) {
            color = 'red lighten-3';
          } else if (index >= 3) {
            color = 'red lighten-4';
          } else if (index >= 1) {
            color = 'red lighten-4';
          }
        }
      }

      return color;
    },

    upDownColor(item) {
      let color = 'grey lighten-5';
      if (item.up_down === 'UP') {
        color = 'green lighten-5';
      } else if (item.up_down === 'DOWN') {
        color = 'red lighten-5';
      } else if (item.up_down === 'BURN') {
        color = 'amber lighten-5';
      }
      return color;
    },

    cToFixed(num, size) {
      try {
        return num.toFixed(size);
      } catch (e) {
        return '';
      }
    },
  },
