<template>
  <div v-if="isLoaded">
    <v-row no-gutters v-for="(tier, rowi) in tiers" :key="'row' + rowi">
      <v-card outlined>
        <v-stage ref="stageView" :config="configKonva">
          <v-layer ref="layer">
            <v-text v-for="(item, i) in tier.priceTexts" :key="'text' + i" :config="item" />

            <v-group v-for="(col, coli) in tier.cols" :key="'col' + coli">
              <v-group
                ref="group"
                v-for="(item, itemi) in col"
                :key="itemi"
                @tap="clickPositionCircle(item)"
                @click="clickPositionCircle(item)"
              >
                <v-text v-if="itemi === 0" :config="item.symbolText" />
                <v-circle v-if="item.positionCircle" :config="item.positionCircle" />
                <v-text
                  v-if="showPositionText(item, itemi, col.length)"
                  :config="item.positionText"
                />
                <v-rect v-else-if="item.nowPriceRect" :config="item.nowPriceRect" />
              </v-group>
            </v-group>
          </v-layer>
        </v-stage>
      </v-card>
    </v-row>
  </div>
</template>

<script>
const Y_TEXT_MARGIN = -5;

import CanvasService from '@/services/CanvasService';

export default {
  name: 'CanvasView',

  components: {},

  data: () => ({
    configKonva: {
      width: 575,
      height: CanvasService.getCanvasHeight(),
      x: 0,
      y: 7,
    },

    tiers: [
      {
        symbols: ['BTCUSDT', 'BTCBUSD', 'BTCUSD_PERP'],
        priceDiv: 5,
      },
      {
        symbols: ['ETHUSDT', 'ETHBUSD', 'ETHUSD_PERP'],
        priceDiv: 12,
      },
      {
        symbols: ['MKRUSDT'],
        priceDiv: 12,
      },
      {
        symbols: ['BNBUSDT', 'BNBBUSD', 'BNBUSD_PERP'],
        priceDiv: 11,
      },
      {
        symbols: ['BCHUSDT', 'XMRUSDT'],
        priceDiv: 5,
      },
      {
        symbols: ['LTCUSDT', 'LTCUSD_PERP', 'DASHUSDT'],
        priceDiv: 13,
      },
      {
        symbols: ['EGLDUSDT', 'EGLDUSD_PERP', 'KSMUSDT'],
        priceDiv: 13,
      },
      {
        symbols: ['FTTUSDT', 'AVAXBUSD', 'AVAXUSD_PERP'],
        priceDiv: 10,
      },
      {
        symbols: ['ETCUSDT', 'ZENUSDT'],
        priceDiv: 10,
      },

      {
        symbols: ['ATOMUSDT', 'UNIUSDT', 'FILUSDT'],
        priceDiv: 18,
      },
      {
        symbols: ['DOTUSDT', 'DOTBUSD', 'DOTUSD_PERP'],
        priceDiv: 18,
      },
      {
        symbols: ['APEUSDT'],
        priceDiv: 18,
      },
      {
        symbols: ['LINKUSDT', 'LINKBUSD', 'ICPBUSD'],
        priceDiv: 18,
      },
      {
        symbols: ['NEARUSDT', 'NEARBUSD', 'FLOWUSDT'],
        priceDiv: 18,
      },
      {
        symbols: ['XTZUSDT', 'OMGUSDT'],
        priceDiv: 7,
      },
      {
        symbols: ['ADAUSDT', 'ADABUSD', 'ADAUSD_PERP'],
        priceDiv: 10,
      },
      {
        symbols: ['MATICUSDT', 'MATICBUSD', 'MATICUSD_PERP'],
        priceDiv: 10,
      },
      {
        symbols: ['1INCHUSDT'],
        priceDiv: 10,
      },
      {
        symbols: ['XRPUSDT', 'XRPBUSD', 'XRPUSD_PERP'],
        priceDiv: 10,
      },
      {
        symbols: ['ALGOUSDT', 'ALGOUSD_PERP', 'IOTAUSDT'],
        priceDiv: 10,
      },
      {
        symbols: ['XLMUSDT'],
        priceDiv: 10,
      },
      {
        symbols: ['DOGEUSDT', 'DOGEBUSD', 'DOGEUSD_PERP'],
        priceDiv: 15,
      },
      {
        symbols: ['TRXUSDT', 'TRXBUSD', 'TRXUSD_PERP'],
        priceDiv: 15,
      },
      {
        symbols: ['HBARUSDT'],
        priceDiv: 15,
      },
      {
        symbols: ['VETUSDT'],
        priceDiv: 10,
      },
      {
        symbols: ['IOSTUSDT', '1000SHIBUSDT', '1000SHIBBUSD'],
        priceDiv: 10,
      },
    ],

    isLoaded: false,
  }),

  computed: {},

  watch: {},

  created() {
    const intViewportWidth = window.innerWidth;
    this.configKonva.width = intViewportWidth - 15;
    this.isLoaded = false;

    console.log(intViewportWidth);
    CanvasService.initTiers(this.tiers);
  },

  methods: {
    createChartData(orders, symbolsObj) {
      console.log('createChartData');

      this.isLoaded = false;

      CanvasService.updateTierMinMax(this.tiers, symbolsObj);

      for (const order of orders) {
        let tierObj = CanvasService.findTier(this.tiers, order.symbol);
        if (tierObj) {
          // console.log(tierObj)
          const xIndex = CanvasService.getXIndex(tierObj.symbols, order.symbol);
          const yIndex = CanvasService.calYindex(
            tierObj.minPrice,
            tierObj.maxPrice,
            order.buyPrice
          );

          let positionXIndex = order.position === 'LONG' ? xIndex : xIndex + 5;

          if (yIndex === 19) {
            positionXIndex = positionXIndex + tierObj.pendingOrder * 20;
            tierObj.pendingOrder++;
          }

          symbolsObj[order.symbol].data.push({
            type: 'position',
            position: order.position,
            status: order.status,
            symbol: order.symbol,
            order: order,
            positionCircle: CanvasService.createPositionCircle(positionXIndex, yIndex, order),
            positionText: CanvasService.createPositionText(positionXIndex, yIndex, order),
            symbolText: CanvasService.createSymbolText(xIndex, order.symbol),
          });

          if (tierObj.addedCount % tierObj.priceDiv === 0) {
            tierObj.priceTexts.push(CanvasService.createPriceText(yIndex, order.buyPrice));
          } else if (tierObj.addedCount === 0) {
            tierObj.priceTexts.push(CanvasService.createPriceText(yIndex, order.buyPrice));
          }

          tierObj.addedCount++;
        }
      }

      for (const symbol in symbolsObj) {
        let tierObj = CanvasService.findTier(this.tiers, symbol);
        if (tierObj) {
          let xIndex = CanvasService.getXIndex(tierObj.symbols, symbol);
          const yIndex = CanvasService.calYindex(
            tierObj.minPrice,
            tierObj.maxPrice,
            symbolsObj[symbol].nowPrice
          );

          xIndex = xIndex - 5;

          symbolsObj[symbol].data.push({
            type: 'nowPrice',
            symbol: symbol,
            nowPriceRect: CanvasService.createNowPriceRect(xIndex, yIndex),
          });

          if (CanvasService.getColIndex(tierObj.symbols, symbol) === 0) {
            tierObj.priceTexts.push(
              CanvasService.createPriceText(yIndex, this.toFixed(symbolsObj[symbol].nowPrice, 2))
            );
          }

          symbolsObj[symbol].data.sort(function (a, b) {
            if (a.positionCircle) {
              return a.positionCircle.y - b.positionCircle.y;
            }
          });

          for (let [index, item] of symbolsObj[symbol].data.entries()) {
            try {
              if (
                item.position === 'LONG' &&
                symbolsObj[symbol].data[index + 1].position === 'SHORT'
              ) {
                item.showPositionText = true;
              } else if (
                item.position === 'SHORT' &&
                symbolsObj[symbol].data[index - 1].position === 'LONG'
              ) {
                item.showPositionText = true;
              }
            } catch (e) {
              console.log('eor');
            }
          }

          tierObj.cols.push(symbolsObj[symbol].data);
        }
      }

      // console.log(this.$refs.stageView)
      // this.$refs.layer.getNode().cache()

      // console.log(symbolsObj)
      console.log(this.tiers);

      setTimeout(() => {
        this.isLoaded = true;
      }, 300);

      // console.log(this.$refs.stageView)
      console.log('setTimeout', this.isLoaded);
    },

    clickPositionCircle(item) {
      this.$emit('click-position', item.order);
    },

    showPositionText(item, itemi, colLength) {
      return (
        item.positionText &&
        (itemi % 4 === 1 ||
          item.showPositionText === true ||
          itemi === 0 ||
          itemi === colLength - 2 ||
          item.status !== 'FILLED')
      );
    },

    toFixed: function (val, num) {
      return Number(Number(val).toFixed(num));
    },
  },
};
</script>

