const Y_TEXT_MARGIN = -5;
const CANVAS_HEIGHT = 380;

const INNER_WIDTH = window.innerWidth - 15;

export default new (class {
  getCanvasHeight() {
    return CANVAS_HEIGHT;
  }

  initTiers(tiers) {
    for (let item of tiers) {
      item.maxPrice = 0;
      item.maxPrice = 0;
      item.minPrice = 999999;
      item.addedCount = 0;
      item.pendingOrder = 0;
      item.priceTexts = [];
      item.cols = [];
    }
  }

  updateTierMinMax(tiers, symbolSummary) {
    for (const item in symbolSummary) {
      let tierObj = this.findTier(tiers, item);
      if (tierObj) {
        if (tierObj.minPrice > symbolSummary[item].minPrice) {
          tierObj.minPrice = symbolSummary[item].minPrice;
        }
        if (tierObj.maxPrice < symbolSummary[item].maxPrice) {
          tierObj.maxPrice = symbolSummary[item].maxPrice;
        }
      }
    }
  }

  findTier(tiers, fullSymbol) {
    for (const tier of tiers) {
      for (const symbol of tier.symbols) {
        if (fullSymbol === symbol) {
          return tier;
        }
      }
    }
  }

  getXIndex(tierSymbols, symbol) {
    let initX = INNER_WIDTH / 5;
    const colGap = INNER_WIDTH / 4;
    let colIndex = 0;
    for (let i = 0; i < tierSymbols.length; i++) {
      if (tierSymbols[i] === symbol) {
        colIndex = i;
      }
    }
    return initX + colGap * colIndex;
  }

  calYindex(minPrice, maxPrice, buyPrice) {
    let curIndex = buyPrice - minPrice;
    let gapIndex = maxPrice - minPrice;

    const HEIGHT_MARGIN = 60;
    const offsetY = 42;
    let maxValue = CANVAS_HEIGHT - HEIGHT_MARGIN;

    let yIndex = Number(((curIndex / gapIndex) * maxValue).toFixed(1));
    // console.log(minPrice,maxPrice,buyPrice,yIndex)
    yIndex = maxValue - yIndex + offsetY;

    if (buyPrice === undefined || isNaN(yIndex)) {
      yIndex = 19;
    }

    return yIndex;
  }

  createPriceText(yIndex, buyPrice) {
    return {
      x: 3,
      y: yIndex + Y_TEXT_MARGIN,
      text: `${buyPrice}`,
      width: 60,
      height: 1,
      fontSize: 10,
      opacity: 0.7,
    };
  }

  createNowPriceRect(xIndex, yIndex) {
    return {
      x: xIndex,
      y: yIndex,
      width: 30,
      height: 1.2,
      opacity: 0.7,
      fill: "black",
    };
  }

  createPositionCircle(xIndex, yIndex, order) {
    let labelColor = order.position === "LONG" ? "green" : "red";
    let radius = 3;

    if (order.status !== "FILLED") {
      labelColor = order.position === "LONG" ? "blue" : "orange";
      radius = 5;
    }

    return {
      x: xIndex,
      y: yIndex,
      radius: radius,
      fill: labelColor,
      opacity: 0.7,
    };
  }

  createPositionText(xIndex, yIndex, order) {
    let positionText = `${Number(order.percent).toFixed(0)} / ${
      order.sell_percent
    }`;
    if (order.status !== "FILLED") {
      positionText = `${order.sell_percent}`;
    }
    return {
      x: xIndex + 6,
      y: yIndex + Y_TEXT_MARGIN,
      // text: `${Number(order.percent).toFixed(0)} / ${order.sell_percent} / ${yIndex.toFixed(0)}`,
      text: positionText,
      width: 50,
      height: 20,
      fontSize: 10,
      opacity: 0.7,
    };
  }

  createSymbolText(xIndex, symbol) {
    return {
      x: xIndex - 5,
      y: -1,
      text: `${symbol}`,
      width: 90,
      height: 20,
      fontSize: 11,
    };
  }

  getColIndex(tierSymbols, symbol) {
    let colIndex = 0;
    for (let i = 0; i < tierSymbols.length; i++) {
      if (tierSymbols[i] === symbol) {
        colIndex = i;
      }
    }
    return colIndex;
  }
})();
