<template>
	<div>
		<v-card class="mx-auto mt-12" max-width="350" v-if="user.length">
			<span>@{{ user[0].telegram_id }}</span>
			<div class="mt-12">Bot list</div>
		</v-card>

		<v-card class="mx-auto" outlined rounded max-width="350">
			<v-list rounded>
				<!-- <v-subheader>Bot list</v-subheader> -->
				<v-list-item-group color="primary">
					<v-list-item v-for="(item, i) in user" :key="i" @click="clickBot(item)">
						<v-list-item-icon>
							<v-icon v-text="item.icon"></v-icon>
						</v-list-item-icon>
						<v-list-item-content>
							<v-list-item-title class="grey--text text--darken-4"
								>{{ item.bot_name }}, {{ item.frame }}</v-list-item-title
							>
							<small class="grey--text">
								<span class="grey--text text--darken-1">{{ item.exchange }}</span> /
								{{ item.email }}</small
							>
						</v-list-item-content>
					</v-list-item>
				</v-list-item-group>
			</v-list>
		</v-card>
	</div>
</template>

<script>
import UserService from '@/services/UserService'

export default {
	data: () => ({
		label: '',
		a: '',
		b: '',
		c: '',
		init_sum: '',

		selectedItem: 1,
		user: [],
		icon: 'mdi-flask-round-bottom',
		items: [
			{ text: 'Real-Time', icon: 'mdi-pine-tree' },
			{ text: 'Audience', icon: 'mdi-flask-round-bottom' },
			{ text: 'Conversions', icon: 'mdi-flask-round-bottom' },
		],
	}),

	created: function () {
		this.telegramId = this.$store.state.telegramId
		this.init()
	},

	methods: {
		async init() {
			let user = await UserService.getUser(this.telegramId)

			user.forEach((item) => {
				if (item.bot_name === 'Tribot') {
					item.icon = 'mdi-pine-tree'
				} else if (item.bot_name === 'Rangebot') {
					item.icon = 'mdi-balloon'
				} else if (item.bot_name === 'Cscalper') {
					item.icon = 'mdi-fire'
				} else if (item.bot_name === 'MacdEma') {
					item.icon = 'mdi-chart-bubble'
				} else {
					item.icon = 'mdi-flask-round-bottom'
				}
			})

			this.user = user

			// console.log(this.user)
		},

		clickBot(item) {
			if (item.bot_name === 'Tribot') {
				if (item.exchange === 'Bybit') {
					this.$router.push({
						path: '/tribot',
						query: { exchange: item.exchange },
					})
				} else if (item.exchange === 'Binance') {
					this.$router.push({
						path: '/tribot/binance',
						query: { exchange: item.exchange },
					})
				}
			} else {
				this.$router.push({
					path: '/range',
					query: { bot_name: item.bot_name, exchange: item.exchange, frame: item.frame },
				})
			}
		},
	},
}
</script>
