<template>
  <div>
    <v-card class="mx-auto mb-2" outlined rounded max-width="980">
      <div class="ml-4 mt-2">
        <small>{{ leftTime }} </small>
      </div>
      <!-- <small class="ml-2">Total : {{ rounds.length }}</small> -->
      <div class="ml-4 my-1">
        <small>UP ({{ summary.upCount }})</small>
        <small class="ml-3">DOWN ({{ summary.downCount }})</small>
      </div>
    </v-card>

    <v-card class="mx-auto mb-2" outlined rounded max-width="980">
      <v-row class="mt-3 mx-1">
        <v-col cols="6">
          <v-text-field dense v-model="orderModel.amt"></v-text-field>
        </v-col>
        <v-col>
          <v-btn small depressed color="green lighten-3" @click="clickRequestOrder('UP')">
            UP
          </v-btn>
          <v-btn
            small
            depressed
            class="ml-5"
            color="red lighten-3"
            @click="clickRequestOrder('DOWN')"
          >
            DOWN
          </v-btn>
        </v-col>
      </v-row>
    </v-card>

    <v-card class="mx-auto" outlined rounded max-width="980">
      <v-data-table
        mobile-breakpoint="200"
        hide-default-footer
        :items-per-page="100"
        :headers="roundsHeaders"
        :items="rounds"
        must-sort
        dense
      >
        <template v-slot:item.up_down="{ item }">
          <v-chip
            class="pa-1"
            :color="winColor(item.up_down, 1, item.up_down, item.status)"
            x-small
          >
            <small>{{ item.up_down }}</small>
          </v-chip>
        </template>

        <template v-slot:item.position_index="{ item }">
          <v-chip
            class="pa-1"
            :color="winColor(item.up_down, item.position_index, item.position, item.status)"
            x-small
          >
            {{ item.position_index }}
            <small>, {{ item.position }}</small>
          </v-chip>
        </template>

        <template v-slot:item.position_index_up="{ item }">
          <v-chip
            class="pa-1"
            :color="winColor(item.up_down, item.position_index_up, item.position_up, item.status)"
            x-small
          >
            {{ item.position_index_up }}
            <small>, {{ item.position_up }}</small>
          </v-chip>
        </template>

        <template v-slot:item.position_index_down="{ item }">
          <v-chip
            class="pa-1"
            :color="
              winColor(item.up_down, item.position_index_down, item.position_down, item.status)
            "
            x-small
          >
            {{ item.position_index_down }}
            <small>, {{ item.position_down }}</small>
          </v-chip>
        </template>

        <template v-slot:item.epoch="{ item }">
          <small>{{ item.epoch }}</small>
        </template>
        <template v-slot:item.ema="{ item }">
          <v-chip class="pa-1" :color="emaColor(item.ema)" x-small>
            {{ item.ema }}
          </v-chip>
        </template>

        <template v-slot:item.status="{ item }">
          <v-chip class="pa-1" :color="statusColor(item.status)" x-small>
            {{ item.status }}
          </v-chip>
        </template>

        <template v-slot:item.lock_timestamp="{ item }">
          <small>{{ item.lock_timestamp }}</small>
        </template>
        <template v-slot:item.amt="{ item }">
          <!-- <small> {{ item.amt }},{{ cToFixed(item.payout, 2) }} </small> -->
          <small> {{ item.amt }} </small>
        </template>
        <template v-slot:item.payout="{ item }">
          <small>{{ cToFixed(item.payout, 2) }}</small>
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="requestDialog" max-width="400px">
      <v-card>
        <v-card-text>
          <v-container>
            <div class="my-5">
              Request order for <b class="text-h6">{{ orderModel.side }} </b>
            </div>

            <v-row>
              <v-col cols="4">
                Amt : <b class="text-h6">{{ orderModel.amt }}</b>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions class="">
          <v-spacer></v-spacer>
          <v-btn small color="grey lighten-3" class="mr-3 mb-3" @click="requestDialog = false">
            Cancel
          </v-btn>
          <v-btn small class="mr-3 mb-3" color="blue darken-3" @click="clickConfirm()" dark>
            Confirm
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import HttpService from '@/services/HttpService';

let intervalFlag = false;
export default {
  data: () => ({
    roundsHeaders: [
      { text: 'Epoch', value: 'epoch' },

      { text: 'UD', value: 'up_down' },

      { text: 'Up', value: 'position_index_up' },
      { text: 'Down', value: 'position_index_down' },

      { text: 'Amt', value: 'amt' },
      { text: 'Status', value: 'status' },
      { text: 'Order', value: 'position_index' },

      { text: 'ema', value: 'ema' },
      //   { text: "payout", value: "payout" },

      // { text: 'time', value: 'lock_timestamp' },
    ],

    rounds: [],
    amts_test1: [],

    summary: {},

    settings: {},
    interval: 0,
    intervalMethod: null,
    lockTimestamp: null,
    leftTime: 0,
    orderModel: { amt: 0.004, side: null },
    requestDialog: false,
  }),

  created: function () {
    //this.getSettings();
    this.init();
    this.intervalMethod = setInterval(() => {
      this.interval++;

      if (this.lockTimestamp) {
        const now = new Date().getTime();
        const target = this.lockTimestamp.getTime();
        const longTime = target - now;

        if (longTime > 0) {
          const tempTime = new Date(longTime);
          this.leftTime = tempTime.getMinutes() + ':' + tempTime.getSeconds();
        } else {
          this.leftTime = this.interval;
        }
      }

      if (this.interval > 6) {
        this.interval = 1;
        this.init();
      }
    }, 1000);
  },

  destroyed: function () {
    console.log('distroyed111');
    clearInterval(this.intervalMethod);
  },

  methods: {
    async init() {
      let rounds = await HttpService.getPlain('https://api.delog.io/api/v1/prediction/rounds');

      this.summary.positionWinCount = 0;
      this.summary.positionCount = 0;
      this.summary.upCount = 0;
      this.summary.downCount = 0;

      for (let [index, item] of rounds.entries()) {
        if (item.up_down === 'UP') {
          this.summary.upCount++;
        } else {
          this.summary.downCount++;
        }

        let date = new Date(item.lock_timestamp);

        if (index === 0) {
          this.lockTimestamp = date;
          item.up_down = 'Next';
        } else if (index === 1) {
          item.up_down = 'Live';
        }

        item.lock_timestamp = this.date2str(new Date(date), 'hh:mm');
        item.price = item.close_price - item.lock_price;

        if (item.ema1) {
          item.ema = Number((item.ema1 - item.ema2).toFixed(2));
        }
      }

      rounds = this.testPositionUp(rounds);
      rounds = this.testPositionDown(rounds);

      this.rounds = rounds;
    },
    testPositionUp(rounds) {
      let position = 'UP';
      this.incPositionCount(rounds, position, 'position_up', 'position_index_up');

      // this.updateSummaryCount(rounds, position, 'position_up', 'position_index_up');
      //const income = this.updateIncome(rounds, 'position_up', 'position_index_up');
      //this.summary.sumPositionUpWin = income.win;
      //this.summary.sumPositionUpLose = income.lose;
      return rounds;
    },

    testPositionDown(rounds) {
      let position = 'DOWN';
      this.incPositionCount(rounds, position, 'position_down', 'position_index_down');

      // this.updateSummaryCount(rounds, position, 'position_down', 'position_index_down');
      // const income = this.updateIncome(rounds, 'position_down', 'position_index_down');
      // this.summary.sumPositionDownWin = income.win;
      // this.summary.sumPositionDownLose = income.lose;
      return rounds;
    },

    incPositionCount(rounds, position, _position, _position_index) {
      for (let i = rounds.length - 3; i >= 0; i--) {
        rounds[i][_position] = position;
        if (rounds[i + 2].up_down === rounds[i + 2][_position]) {
          rounds[i][_position_index] = 1;
        } else {
          rounds[i][_position_index] = rounds[i + 2][_position_index] + 1;
        }
      }
    },

    updateIncome(rounds, _position, _position_index) {
      let sumTestPositionShortWin = 0;
      let sumTestPositionShortLose = 0;
      for (let i = rounds.length - 3; i >= 0; i--) {
        let fee = 0.00047;

        if (rounds[i][_position] === 'UP' || rounds[i][_position] === 'DOWN') {
          let tempAmt = this.amts_test1[rounds[i][_position_index] - 1];

          if (rounds[i].up_down === rounds[i][_position]) {
            let payout = 0;
            if (rounds[i].up_down === 'UP') {
              payout = rounds[i].bear_amt / rounds[i].bull_amt + 1;
            } else {
              payout = rounds[i].bull_amt / rounds[i].bear_amt + 1;
            }

            let testIncome2 = tempAmt * payout * 0.97 - tempAmt;
            if (testIncome2 > 0) testIncome2 += fee;

            if (tempAmt !== undefined) {
              sumTestPositionShortWin += testIncome2;
              rounds[i].amt_test = testIncome2;
            }
          } else {
            let testIncome2 = -tempAmt;
            if (testIncome2 > 0) testIncome2 += fee;
            if (tempAmt !== undefined) {
              sumTestPositionShortLose += testIncome2;
              rounds[i].amt_test = testIncome2;
            }
          }
        }
      }
      return { win: sumTestPositionShortWin, lose: sumTestPositionShortLose };
    },

    async getSettings() {
      let settings = await HttpService.get('/pish/prediction/rounds2/settings');
      this.settings = settings[0];
      //   console.log(settings);
    },

    clickRequestOrder(side) {
      console.log(side);
      this.orderModel.side = side;
      this.requestDialog = true;

      // await HttpService.put('/pish/prediction/rounds2/settings', this.settings);
    },

    async clickConfirm() {
      this.requestDialog = false;
      const telegramId = this.$store.state.telegramId;
      console.log(telegramId);
      this.orderModel.telegramId = telegramId;
      console.log(this.orderModel);

      const result = await HttpService.postPlain(
        'https://api.delog.io/api/v1/prediction/enter',
        this.orderModel
      );
      console.log(result);
    },

    async clickUpdateSetting() {
      await HttpService.put('/pish/prediction/rounds2/settings', this.settings);
    },

    async clickUpdateLong() {
      this.settings.long_short = 1;
      await HttpService.put('/pish/prediction/rounds2/settings/longshort', this.settings);
    },

    async clickUpdateShort() {
      this.settings.long_short = 0;
      await HttpService.put('/pish/prediction/rounds2/settings/longshort', this.settings);
    },

    getPercent(total, count) {
      return ((count / total) * 100).toFixed(1);
    },

    emaColor(ema) {
      if (ema) {
        return ema > 0 ? 'green lighten-5' : 'red lighten-5';
      }
    },

    statusColor(item) {
      if (item === 'SKIP' || item === null) {
        return 'white';
      } else {
        return item === 'SUCCESS' ? 'green lighten-5' : 'red lighten-5';
      }
    },

    winColor(up_down, index, position, status) {
      let color = 'white lighten-3';

      if (up_down === position) {
        if (up_down === 'UP') {
          if (index >= 8) {
            color = 'green lighten-2';
          } else if (index >= 6) {
            color = 'green lighten-3';
          } else if (index >= 4) {
            color = 'green lighten-4';
          } else if (index >= 1) {
            color = 'green lighten-5';
          }
        } else if (up_down === 'DOWN') {
          if (index >= 8) {
            color = 'red lighten-2';
          } else if (index >= 6) {
            color = 'red lighten-3';
          } else if (index >= 4) {
            color = 'red lighten-4';
          } else if (index >= 1) {
            color = 'red lighten-5';
          }
        }
      } else {
        if (index >= 5) {
          color = 'blue lighten-4';
        } else if (index >= 4) {
          color = 'blue lighten-5';
        }
      }
      return color;
    },

    date2str(x, y) {
      var z = {
        M: x.getMonth() + 1,
        d: x.getDate(),
        h: x.getHours(),
        m: x.getMinutes(),
        s: x.getSeconds(),
      };
      y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
        return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2);
      });

      return y.replace(/(y+)/g, function (v) {
        return x.getFullYear().toString().slice(-v.length);
      });
    },

    cToFixed(num, size) {
      try {
        return num.toFixed(size);
      } catch (e) {
        return '';
      }
    },

    cSubstr(txt, index) {
      let tResult = '';
      try {
        tResult = txt.substring(0, index).toLowerCase();
      } catch (e) {
        console.log('error');
      }
      return tResult;
    },
  },
};
</script>
