			{ text: 'l.pen', value: 'longPendingCount' },
			{ text: 's.pen', value: 'shortPendingCount' },
			{ text: 'l.fil', value: 'longFilledCount' },
			{ text: 's.fil', value: 'shortFilledCount' },
			{ text: 'Created', value: 'createdCount' },
			{ text: 'Trade', value: 'total' },
		],

		expanded: [],
		editedItem: {
			symbol: '',
