	},

	methods: {
		async getOrderList() {
			this.summaryText.buyPriceTotal = 0
			this.summaryText.sellPriceTotal = 0
			this.summaryText.buyCountTotal = 0
			this.summaryText.sellCountTotal = 0

			let orders = ''
			let positions = ''

			if (this.$route.query.exchange === 'Bybit') {
				orders = await HttpService.get('/coin/bybit/tribot/orders')
				positions = await HttpService.get('/coin/bybit/tribot/positions')
			} else if (this.$route.query.exchange === 'Binance') {
				console.log('binance')
			}

			this.originalPositions = positions

			this.tempLast3h = {}
			this.summaryList = []

			for (let order of orders) {
				if (this.tempLast3h[order.symbol] === undefined) {
					this.tempLast3h[order.symbol] = {
						buyCount: 0,
						sellCount: 0,
						createdCount: 0,
						longCreatedCount: 0,
						shortCreatedCount: 0,
						longFilledCount: 0,
						shortFilledCount: 0,
						pendingCount: 0,
						longPendingCount: 0,
						shortPendingCount: 0,
					}
				}

				let isFindPosition = false

				for (let position of positions) {
					if (order.id === position.oi) {
						order.bs = position.bs
						order.buy_price = position.bp
						order.ss = position.ss
						order.sell_price = position.sp
						order.profit = ((order.sell_price - order.buy_price) / order.buy_price) * 100
						order.profit = this.toFixed(order.profit, 2)

						order.price = order.buy_price * order.amt
						order.price = this.toFixed(order.price, 2)

						if (order.side === 'Buy') {
							this.summaryText.buyPriceTotal += order.price
						} else if (order.side === 'Sell') {
							this.summaryText.sellPriceTotal += order.price
						}

						if (order.bs === 'New' || order.bs === 'Created') {
							this.tempLast3h[order.symbol].createdCount++
							if (order.side === 'Buy') {
								this.tempLast3h[order.symbol].longCreatedCount++
							} else if (order.side === 'Sell') {
								this.tempLast3h[order.symbol].shortCreatedCount++
							}
						}

						if (position.bs === 'Filled') {
							if (order.side === 'Buy') {
								this.tempLast3h[order.symbol].longFilledCount++
							} else if (order.side === 'Sell') {
								this.tempLast3h[order.symbol].shortFilledCount++
							}
						}

						isFindPosition = true
						break
					}
				}

				if (!isFindPosition) {
					this.tempLast3h[order.symbol].pendingCount++
					if (order.side === 'Buy') {
						this.tempLast3h[order.symbol].longPendingCount++
					} else if (order.side === 'Sell') {
						this.tempLast3h[order.symbol].shortPendingCount++
					}
				}

				order.shortSymbol = order.symbol.split('USDT')[0]
			}

			let ticker = ''

			if (this.$route.query.exchange === 'Bybit') {
				ticker = await HttpService.get('/coin/bybit/tribot/ticker')
			} else if (this.$route.query.exchange === 'Binance') {
				console.log('binance')
			}

			for (const key in ticker) {
				const shortSymbol = key.split('USDT')[0]
				orders.push({
					symbol: key,
					shortSymbol: shortSymbol,
					bs: 'Ticker',
					buy_price: ticker[key].price,
					side: '---',
				})
			}

			this.orders = orders

			let tempPositions = []
			for (let position of positions) {
				let isFind = false

				for (let order of orders) {
					if (order.id === position.oi) {
						isFind = true
						break
					}
				}

				if (!isFind) {
					position.shortSymbol = position.symbol.split('USDT')[0]
					position.profit = (((position.sell_price - position.buy_price) / position.buy_price) * 100).toFixed(
						2
					)
					tempPositions.push(position)
				}

				// let find = this.$underscore.find(orders, { symbol: key })
				// if(find){
				// 	console.log(find)
				// }
			}

			for (let key in this.tempLast3h) {
				const shortSymbol = key.split('USDT')[0]
				this.summaryList.push({
					symbol: key,
					shortSymbol: shortSymbol,
					total: 0,
					createdCount: this.tempLast3h[key].createdCount,
					pendingCount: this.tempLast3h[key].pendingCount,
					longPendingCount: this.tempLast3h[key].longPendingCount,
					shortPendingCount: this.tempLast3h[key].shortPendingCount,
					longCreatedCount: this.tempLast3h[key].longCreatedCount,
					shortCreatedCount: this.tempLast3h[key].shortCreatedCount,

					longFilledCount: this.tempLast3h[key].longFilledCount,
					shortFilledCount: this.tempLast3h[key].shortFilledCount,
				})
			}

			this.positions = tempPositions
			this.getHistories()
		},

		async getHistories() {
			let histories = []

			if (this.$route.query.exchange === 'Bybit') {
				histories = await HttpService.get('/coin/bybit/tribot/histories')
			} else if (this.$route.query.exchange === 'Binance') {
				console.log('binance')
			}

			let tempProfitSum = 0

			for (let item of histories) {
				tempProfitSum += item.profit
				item.shortSymbol = item.symbol.split('USDT')[0]
				// item.profit =  ((item.sell_price - item.buy_price) / item.buy_price * 100).toFixed(2)
				item.newDate = this.date2str(new Date(item.created_date), 'hh:mm')
				this.summaryText.lastTime = item.newDate

				let time = new Date(item.created_date).getTime()

				if (item.side === 'Buy') {
					this.tempLast3h[item.symbol].buyCount++
				} else {
					this.tempLast3h[item.symbol].sellCount++
				}

				if (item.side === 'Buy') {
					this.summaryText.buyCountTotal++
				} else {
					this.summaryText.sellCountTotal++
				}
			}
			this.summaryText.profitSum = tempProfitSum

			// this.histories = histories
			this.$refs.historyView.setHistories(histories)

			for (const item of this.summaryList) {
				item.h3BuyCount = this.tempLast3h[item.symbol].buyCount
				item.h3SellCount = this.tempLast3h[item.symbol].sellCount
				item.total = item.h3BuyCount + item.h3SellCount
			}
		},

		onUpdatedOrder(type, order) {
			// console.log('onUpdatedOrder in Tribot')
			// console.log(type)
			// console.log(order)

			let copiedObj = this.$underscore.clone(order)

			if (type === 'New') {
				copiedObj.id = copiedObj.id + 9999
				copiedObj.bs = null
				copiedObj.buy_price = null
				copiedObj.ss = null
				copiedObj.sell_price = null

				let findSummary = this.$underscore.find(this.summaryList, { symbol: order.symbol })
				if (findSummary) {
					findSummary.pendingCount++
					if (copiedObj.side === 'Buy') {
						findSummary.longPendingCount++
					} else if (copiedObj.side === 'Sell') {
						findSummary.shortPendingCount++
					}
				}

				this.orders.push(copiedObj)
				this.expanded = []
			}

			// console.log(this.orders)
		},

		clickSnackbarBtn(mode) {
			if (mode === 'New') {
				this.itemDialog.title = 'New Item'
			} else if (mode === 'Edit') {
				this.itemDialog.title = 'Edit Item'
			} else if (mode === 'Cancel') {
				this.itemDialog.title = 'Cancel Item'
			}

			this.itemDialog.show = true
			this.itemDialog.mode = mode
			this.showSnackBar = false
		},

		async clickDialogBtn() {
			if (this.itemDialog.mode === 'New') {
				console.log(this.editedItem)

				if (this.$route.query.exchange === 'Bybit') {
					await HttpService.post('/coin/bybit/tribot/orders', this.editedItem)
				} else if (this.$route.query.exchange === 'Binance') {
					console.log('binance')
				}

				// this.updatePosition()
			}

			// else if (this.itemDialog.mode === 'Edit') {
			// 	await HttpService.put('/coin/bybit/tribot/orders', this.editedItem)
			// 	this.updatePosition()
			// } else if (this.itemDialog.mode === 'Cancel') {
			// 	await HttpService.delete('/coin/bybit/tribot/cancel/orders/' + this.editedItem.id)
			// 	if (this.editedItem.bs === 'New') {
			// 		this.editedItem.bs = 'CancelRequested'
			// 	} else {
			// 		this.updatePosition()
			// 	}
			// }
			this.closeDialog()
		},

		closeDialog() {
			this.itemDialog.show = false
		},

		createOrdersBySymbol(item) {
			const orders = this.orders.filter((v) => {
				if (v.symbol === item.symbol) {
					return v
				}
			})
			return orders
		},

		priceColor(item) {
			let color = ''
			if (item === 'Created') {
				color = 'amber lighten-4'
			} else if (item === 'New') {
				color = 'amber lighten-2'
			} else if (item === 'Filled') {
				color = 'blue lighten-3'
			} else if (item === 'PartiallyFilled') {
				color = 'blue lighten-4'
			} else if (item === 'Cancelled') {
				color = 'red lighten-2'
			} else if (item === 'Ticker') {
				color = 'transparent'
			} else {
				color = 'transparent'
			}
			return color
		},

		// sideColor(side) {
		// 	let color = ''
		// 	if (side === 'Buy') {
		// 		color = 'green lighten-4'
		// 	} else if (side === 'Sell') {
		// 		color = 'red lighten-4'
		// 	} else {
		// 		color = 'white'
		// 	}
		// 	return color
		// },

		clickSummaryRow(row) {
			if (this.expanded.length && row.symbol === this.expanded[0].symbol) {
				this.expanded = []
			} else {
				this.expanded = [row]
			}
		},

		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
